.page {
  font-family: "Open Sans", "Arial", sans-serif;
  color: #444444;
  background-color: #C7C7C7;
  margin: 0;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  background-color: #FFFFFF;
  padding: 0;
  margin: 0 auto;
  box-sizing: border-box;
}
.button {
  width: 260px;
  height: 52px;
  font-size: 18px;
  line-height: 20px;
  font-weight: 700;
  text-transform: uppercase;
  color: #444444;
  background-color: #FFFFFF;
  border: 2px solid #444444;
  cursor: pointer;
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}
.reset-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.header {
