.application__wrapper {
  background-color: @darkwhite;
}

.application__container {
  width: 320px;

  position: relative;
  padding: 73.5px 0;
  margin: 0 auto;
  box-sizing: border-box;

  @media (min-width: @tablet-width) {
    width: 768px;

    padding: 100px 0 75px;
  }

  @media (min-width: @desktop-width) {
    width: 1150px;

    padding: 101px 0 96px;
  }
}

.application__container::before {
  @media (min-width: @tablet-width) {
    content: "";

    position: absolute;
    bottom: -12px;
    left: 0;

    width: 768px;
    height: 24px;

    background-image: url("../img/general/zigzag-tablet-white.svg");
  }

  @media (min-width: @desktop-width) {

    bottom: -17px;
    left: 0;

    width: 1150px;
    height: 34px;

    background-image: url("../img/general/zigzag-desktop-white.svg");
  }
}

.application__title {
  font-size: 27px;
  line-height: 30px;
  font-weight: 700;
  text-align: center;

  color: @black;

  margin: 0;
  box-sizing: border-box;

  @media (min-width: @tablet-width) {
    font-size: 43px;
    line-height: 50px;
  }

  @media (min-width: @desktop-width) {
    font-size: 53px;
    line-height: 57px;
  }
}

.application__text {
  width: 320px;

  font-size: 13px;
  line-height: 24px;
  font-weight: 400;

  color: @grey;

  padding: 17px 30px 15px;
  margin: 0 auto;
  box-sizing: border-box;

  @media (min-width: @tablet-width) {
    width: 768px;
    font-size: 17px;
    line-height: 30px;

    padding: 66px 50px 30px;
  }

  @media (min-width: @desktop-width) {
    width: 1150px;

    padding: 115.5px 225px 40px;
  }
}

.application__text .application__text-mobile-br {
  @media (min-width: @tablet-width) {
    display: none;
  }
}

.application__text .application__text-tablet-br {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
  }

  @media (min-width: @desktop-width) {
    display: none;
  }
}

.application__text .application__text-desktop-br {
  display: none;

  @media (min-width: @desktop-width) {
    display: block;
  }
}

.application__form {
  font-size: 17px;
  line-height: 18px;
  font-weight: 700;
}

.application__group {
  line-height: 16px;

  border: none;

  padding: 0;
  margin: 0;
}
